export const required = (value) => {
    if (value) return undefined

    return ' '
}

export const maxLengthCreator = (maxLength) => (value) => {
    if (value.length > maxLength) return `  ${maxLength} `

    return undefined
}

export const minLengthCreator = (minLength) => (value) => {
    if (value.length < minLength) return `  ${minLength} `

    return undefined
}

export const emailValid = (value) => {
    if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value) && !!value) {
        return '  email'
    }
}


export const regMatchInput = (input, allInputs) => input === allInputs.regPassword ? undefined : '  ';

export const checkOldPass = (oldPassword) => (value) => {
    if (value !== oldPassword) return `  `

    return undefined
}
export const newPassMatchInput = (input, allInputs) => input === allInputs.newPassword ? undefined : '  ';

export const newCannotBeOld = (input, allInputs) => input !== allInputs.oldPassword ? undefined : '      ';

